.main {
  background-color: rgb(245, 245, 236);
  padding: 30px;
}

h1,
h2,
h3 {
  margin-top: 100px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  text-align: center;
  color: #073b01;
}

h1,
h3 {
  margin-top: 50px;
  width: 100%;
}

@media only screen and (min-width: 401px) {
  .image-gallery {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    height: 200px;
  }

  /* Individual image */
  .image-gallery img {
    width: auto;
    height: 100%;
    object-fit: cover;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 400px) {
  .image-gallery {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    height: 200px;
  }

  .image-gallery img {
    width: auto;
    height: auto;
    max-width: 100%;
    margin-bottom: 5px;
  }

  .scroll-indicator {
    display: none;
  }
}
